.app {
  height: 100vh;
  display: flex;
  background-color: black;
  color: white;
}
.main{
  width: 75%;
  background: url('assets/bg.jpg') center;
  display: flex;
  flex-direction: column;
}

.top{
  height: 50%;
}

.timer{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 5px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: 700;
  position: absolute;
  bottom: 300px; 
  left: 80px;
}
.bottom{
  height: 50%;
}

.pyramid{
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.moneyList {
  width: 100%;
  list-style: none;
  padding: 20px;
}
.moneyListItem {
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
}
.moneyListItem.active {
  background-color: teal;
}

.moneyListItemNumber{
  font-size:16px;
  width: 30%;
  font-weight: lighter;
}
.moneyListItemAmount{
  font-size:18px;
  font-weight: 400;
}

.qusAns {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.question {
  width: 80%;
  background: linear-gradient(#100241, black);
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  border: 2px solid white;
  font-size: 20px;
}

.answers {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.answer {
  width: 40%;
  padding: 10px;
  margin: 0 10px 20px 10px;
  text-align: center;
  background: linear-gradient(#0e0124, #22074d);
  border: 1px solid white;
  border-radius: 15px;
  font-weight: 300;
  font-size: 20px;
  cursor: pointer;
}

.answer:hover,
.answer.active {
  background: mediumblue;
}

.answer.correct {
  animation: correct 2s ease forwards;
}

@keyframes correct {
  0%,
  22%,
  42% {
    background: mediumblue;
  }
  20%,
  40%,
  60% {
    background: linear-gradient(#0e0124, #22074d);
  }
  62%,
  100% {
    background: green;
  }
}

.answer.wrong {
  animation: wrong 2s ease forwards;
}

@keyframes wrong {
  0%,
  22%,
  42% {
    background: mediumblue;
  }
  20%,
  40%,
  60% {
    background: linear-gradient(#0e0124, #22074d);
  }
  62%,
  100% {
    background: crimson;
  }
}

.endText {
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.start {
  width: 250px !important;
  height: 100px !important;
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
  justify-content: space-around !important;
  position: relative !important;
  top: -110px !important;
  bottom: 0 !important;
  left: 0 !important;
  right: 0 !important;
  margin: auto !important;
}

.startInput {
  width: 100% !important;
  height: 30px !important;
  border: none !important;
  border-radius: 5px !important;
  text-align: center !important;
  font-size: 18px !important;
  margin-bottom: 20px;
}

.startInput:focus {
  outline: none !important;
}

.startButton {
  width: 100% !important;
  height: 30px !important;
  border: none !important;
  border-radius: 5px !important;
  cursor: pointer !important;
  font-size: 18px !important;
  font-weight: 500 !important;
}